/*config*/

body{font-family:'Open Sans', sans-serif}
body{font-size: 18px}
body{color:#323E4E}
body{font-weight:400}
body{font-style:normal}



/* End config */
 
header {position: relative; display:inline-block; 
width:100%; background:#323E4E; color:white;height:120px; padding:10px}


header .logo {
position:absolute; 
font-size:40px;
top:25%; 
left:25px; 
color:white;}

nav {
position: absolute;
display: block;
top: 0px;
right:0px;
width:20%;
}

.picture {
	position: absolute;
	left: 300px;
}

li.a:hover {
	background-color:#8bb2c6;
	color: white;
	text-decoration:none;
}

a {
	color: white; 
	cursor: pointer;
}

a:hover {
	background: white;
	color: black;
}

a.active { background-color: #8bb2c6; color: white;}

ul {list-style-type: none; margin: 0; padding: 0; background-color:#BED4DF;}

a{color:#323E4E; text-decoration: underline; display:block;}

.container-fluid{ padding: 20px;}

.utdPicture {

}

.scoot {
	color:#323E4E; text-decoration: underline;
	padding:20px;
}

blockquote{
	margin: 20px;
	line-height: 1.5;
	padding:10px;
	background-color: #BED4DF;
	letter-spacing: .25px;
	text-line: center;
}

h2{
	height:40px;
	font-size: 30px;
	margin-bottom:20px;
	
}

h1{
	height: 35px;	
	font-size: 22px;
	margin-bottom:20px;
}



.links{
font-size:25px;
padding-left:40px;
text-align: center;
}

.links_scoot{
text-align: left;
}


.footerHolder { 
position:fixed;
bottom: 0;
width: 100%;
text-align: center; 
background: #323E4E;
color:white;
margin-top:auto;
}


nav ul li a {
	display:block;
	text-align: center;
	text-decoration:none;
	font-size:20px;
	letter-spacing:.25px;
}

.about{
display: flex;

}

.aboutLeft{
	flex:1;
	float:left;
	width:50%;
}


.aboutRight{ 
float:right;
margin-top:45px;
margin-bottom: 50px;

}

.aboutPara{
padding-left:20px;


}

.utdTeamPicture{
	text-align: center;
}

.clickHere{
	text-align: right;
	padding-right:20%;
	margin-top:-10px;
}

